<template>
	<div class="page-case">
		<v-header curPge="case"></v-header>
		<div class="about">
			<div class="about-wrapper">
				<div class="about-us">产品案例</div>
				<div class="about-desc">产品&解决方案</div>
			</div>
		</div>
		<div class="case-list">
			<div class="case-wrapper">
				<ul class="flex pdt-24 pdb-24 text-left">
					<li>分类：</li>
					<li class="item-active">全部</li>
					<li>区块链</li>
					<li>物联网</li>
				</ul>
				<div class="case-item-wrapper flex-and-wrap">
					<div v-for="img in imgs" class="case-item toUp animated" @click="detail" >
						<a href="javascript:;">
							<img width="100%" height="100%" v-lazy="img" :loading="dafaultImg" :error="dafaultImg" lazy="loading">
						</a>
						<div class="case-name">案列名称</div>
					</div>
				</div>
			</div>
		</div>
		
		<v-footer />
	</div>	
</template>
<script>
	import Vue from 'vue'
	import VueLazyload from 'vue-lazyload'
	import Header from "@/components/header.vue"
	import Footer from "@/components/footer.vue"
	Vue.use(VueLazyload)
	export default{
		name:"case",
		components:{
      		"v-header":Header,
      		"v-footer":Footer
      	},
		data(){
			return {
				imgs:["https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572255869055&di=23c1a3925862ab2ba413a88fc8ee2cb1&imgtype=0&src=http%3A%2F%2Fdik.img.kttpdq.com%2Fpic%2F75%2F52446%2Ff820e136aa5c3384_2560x1440.jpg","https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572256986878&di=3d63c0b1bcbad640fb0a2f062fb3645b&imgtype=0&src=http%3A%2F%2Fbpic.ooopic.com%2F16%2F00%2F46%2F16004647-54f4113b63462bc38b6589541a66aff5.jpg",require("../../assets/img/temp/case.png"),require("../../assets/img/temp/case.png"),require("../../assets/img/temp/case.png"),"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572256986878&di=3d63c0b1bcbad640fb0a2f062fb3645b&imgtype=0&src=http%3A%2F%2Fbpic.ooopic.com%2F16%2F00%2F46%2F16004647-54f4113b63462bc38b6589541a66aff5.jpg","https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572256986878&di=3d63c0b1bcbad640fb0a2f062fb3645b&imgtype=0&src=http%3A%2F%2Fbpic.ooopic.com%2F16%2F00%2F46%2F16004647-54f4113b63462bc38b6589541a66aff5.jpg","https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572256986878&di=3d63c0b1bcbad640fb0a2f062fb3645b&imgtype=0&src=http%3A%2F%2Fbpic.ooopic.com%2F16%2F00%2F46%2F16004647-54f4113b63462bc38b6589541a66aff5.jpg","https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572256986878&di=3d63c0b1bcbad640fb0a2f062fb3645b&imgtype=0&src=http%3A%2F%2Fbpic.ooopic.com%2F16%2F00%2F46%2F16004647-54f4113b63462bc38b6589541a66aff5.jpg"],
				dafaultImg:require("../../assets/img/temp/loading.gif")
			}
		},
		methods:{
			detail(){
				this.$router.push("/detail")
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../assets/scss/mixin';
	.page-case{
		.about{
			width: 100%;
			height: px2vw(320);
			background: url("../../assets/img/icons/about.png") no-repeat;
			background-size: 100% 100%;

			.about-wrapper{
				width: 100%;
				height: 100%;
				background: rgba(0,12,63,0.7);
				padding-left: px2vw(370);
				text-align: left;
				padding-top: px2vw(84);
				color:rgba(255,255,255,1);
			}
			.about-us{
				font-size:px2vw(40);
				font-weight:bold;
			}
			.about-desc{
				font-size:px2vw(18);
				font-weight:400;
				margin-top: px2vw(16);
			}
		}
		.case-list{
			width: 100%;
			background: #EFF3F8;
			.case-wrapper{
				width: px2vw(1200);
				margin: 0 auto;
				li{
					margin-right: px2vw(60);
					font-size: px2vw(14);
					cursor: pointer;
				}
			}
			.case-item{
				width: px2vw(280);
				height: px2vw(354);
				background: #3598DB;
				background-size: 100% 100%;
				margin-right: px2vw(19);
				margin-bottom: px2vw(20);
				position: relative;
				box-shadow: 4px 4px 5px rgba(0,0,0,0.2);
				overflow: hidden;
  				img{
  					transition: transform 0.75s;
  				}
				img:hover{
					transform: scale(1.1);
				}
				a{
					height: calc(100% - 70px);
					overflow: hidden;
				}
				a:hover {
				    text-decoration: none;
				    color: #c89a69;
				}
			}
			.case-name{
				position: absolute;
				width: 100%;
				height: px2vw(70);
				line-height: px2vw(70);
				bottom: 0;
				left: 0;
				overflow: hidden;
				background: #fff;
				padding-left: px2vw(12);
				font-size: px2vw(18);
			}
		}
		.item-active{
			color: #1ccdff;
		}
		// img[lazy=loading]{
		// 	background: url("../../assets/img/temp/loading.gif") center center no-repeat;
		// 	background-size: 100% 100%;
		// }
		.toUp{
			animation-duration: 1s;
			animation-name: toUp;
			animation-delay: 0.2s;
		}
		@keyframes toUp{
			0%{
				opacity: 0;
				transform: translateY(20px);
			}
			100%{
				opacity: 1;
				transform: translateY(0);
			}
		}
	}
</style>


