<template>
    <header class="header flex">
       <span class="wid-30 flex text-left"><img src="../assets/img/icons/logo.png" height="45"></span>
        <!--  <span class="grow flex text-right pointer" @click="goRouter('/')"><span>首页</span></span>
        <span class="grow flex text-right pointer" @click="goRouter('/product')"><span :class="{'on':curPge=='product'}">产品&解决方案</span></span>
        <span class="grow flex text-right pointer" @click="goRouter('/case')"><span :class="{'on':curPge=='case'}">产品案例</span></span>
        <span class="grow flex text-right pointer" @click="goRouter('/support')"><span :class="{'on':curPge=='support'}">技术支持</span></span>
        <span class="grow flex text-right pointer" @click="goRouter('/about')"><span :class="{'on':curPge=='about'}">关于我们</span></span> -->
        <ul class="flex grow">
            <li class="li" @click="goRouter('/')">首页</li>
            <li class="li" :class="{'on':curPge=='product'}" @click="goRouter('/product')">产品&解决方案</li>
            <li class="li" :class="{'on':curPge=='case'}"  @click="goRouter('/case')">产品案例</li>
            <li class="li" :class="{'on':curPge=='support'}" @click="goRouter('/support')">技术支持</li>
            <li class="li" :class="{'on':curPge=='about'}" @click="goRouter('/about')">关于我们</li>
        </ul>
    </header>
</template>

<script>
    export default {
        data() {
            return {}
        },
        props:{
            curPge:{
                type:String
            }
        },
        methods:{
            goRouter(router){
                this.$router.push(router);
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import '../assets/scss/mixin';
    .header{
        width: px2vw(1200);
        min-width: 960px;
        margin: 0 auto;
        height: px2vw(70);
        line-height: px2vw(70);
        ul{
            width: 30%;
            height: px2vw(70);
            justify-content: flex-end;
        }
        .li{
            padding: 0 20px;
            display: flex;
            position: relative;
            height: px2vw(70);
            cursor: pointer;
            &:hover{
                color:rgba(28,205,255,1);
                &:before{
                    content: "";
                    position: absolute;
                    width: 0;
                    height: 2px;
                    bottom: 0;
                    left: 0;
                    background: rgba(28,205,255,1);
                    animation: borderSilder 0.6s;
                }
            }
        }
        .on{
            color:rgba(28,205,255,1);
            &:after{
                content: "";
                position: absolute;
                width: 100%;
                height: 2px;
                bottom: 0;
                left: 0;
                background: rgba(28,205,255,1);
            }
        }
    }

    @keyframes borderSilder{
        0% {width: 0;}
        100% {width: 100%}
    }
</style>



