				习近平沿滨江栈桥步行察看黄浦江两岸风貌，了解人文历史和城乡变迁，走进雨水花园实地考察城市环境综合治理和海绵城市建设等情况，并乘车沿途察看渔人码头、杨树浦港旧址等地标景观，对杨浦区科学改造滨江空间、打造群众公共休闲活动场所的做法表示肯定。在参观途中，遇到许多正在休闲健身的市民，习近平同他们亲切交谈。习近平指出，城市是人民的城市，人民城市为人民。无论是城市规划还是城市建设，无论是新城区建设还是老城区改造，都要坚持以人民为中心，聚焦人民群众的需求，合理安排生产、生活、生态空间，走内涵式、集约型、绿色化的高质量发展路子，努力创造宜业、宜居、宜乐、宜游的良好环境，让人民有更多获得感，为人民创造更加幸福的美好生活。
			</div>
			<div class="paragraph">
			　　位于浦江岸边的人人屋党群服务站是一个开放式党建服务平台，习近平向服务站工作人员和居民详细询问这里开展基层党建和公共服务情况。一位年近九旬的老党员、老劳模激动地向总书记介绍说，自己是土生土长的上海人，见证了上海从旧中国到新中国、再到新时代的巨大变化，为中国共产党和中国特色社会主义感到无比自豪。习近平鼓励她多向年轻人讲一讲，坚定他们对中国特色社会主义的道路自信、理论自信、制度自信、文化自信。
			</div>

			<div class="flex pdt-30 pdb-30">
